}

.accordion .text-decoration-none.collapsed:after{
    position: absolute;
    right: -15px;
    top: -2px;
    display: block;
    content: "    ";
    width: 32px;
    height: 32px;
    background:url("down.png") center no-repeat;
    transform: rotate(-90deg);
    -webkit-transform: rotate(-90deg);
    background-size: 25px 15px;
}

