  .card-container, .card, .card .side {
    width: 100%;
    height: 100%;
  }
  .card-container {
    cursor: pointer;
    perspective: 600px;
  }
  .card {
    border-radius: 15px;
    position: relative;
    transition: transform 0.8s, box-shadow 0.2s, border-color 0.2s;
    transform-style: preserve-3d;
    box-shadow: 0 0 3px rgba(0, 0, 0, .15);
    border: 2px solid #bababa;
  }
  
  @media (max-width: 991.98px) {
    .card {
      border-radius: 5px;
   }
  }
  .card .side {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 13px;
  }
  @media (max-width: 991.98px) {
    .card .side {
      border-radius: 4.3333333333px;
   }
  }
  .card .back {
    transform: rotateY(180deg);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(45deg, #1fa5ff 25%, #1053ff 25%, #1053ff 50%, #1fa5ff 50%, #1fa5ff 75%, #1053ff 75%, #1053ff 100%);
    background-size: 56px 56px;
  }
  .card .back::before, .card .back::after {
    border-radius: 13px;
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(#20e4a4, #2cc9cc);
    opacity: 0.8;
  }
  @media (max-width: 991.98px) {
    .card .back::before, .card .back::after {
      border-radius: 4.3333333333px;
   }
  }
  .card .back::after {
    opacity: 0;
    transition: opacity 0.4s;
  }
  .card-container:hover .card {
    border: 2px solid #00be85;
    box-shadow: 0 0 10px rgba(0, 190, 133, .6);
  }
  .card-container:hover .card .back::after {
    opacity: 0.5;
  }
  .card-container:hover .card.flipped {
    border: 2px solid #00a573;
  }
  .card.flipped {
    border: 2px solid #008b61;
    transform: rotateY(180deg);
  }
