  html, body, #root {
    width: 100vw;
    height: 100vh;
  }
  html {
    line-height: 1;
    font-size: 14px;
    overflow-x: hidden;
    color: #1e2021;
  }
  a {
    color: #1e2021;
    text-decoration: none;
  }
  .game {
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
  }

  .game .cards-container {
    width: calc(100vw - (1024 * 12));
    height: calc(100vh - (1024 * 12));
    margin: 96px;
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 16px;
  }

  @media screen and (max-width: 450px) and (min-height: 650px) {
    .game .cards-container {
      grid-template-columns: repeat(4, auto);
      grid-template-rows: repeat(5, 1fr);
   }
  }
  @media screen and (min-width: 1400px) and (max-height: 800px) {
    .game .cards-container {
      grid-template-columns: repeat(9, auto);
      grid-template-rows: repeat(2, 1fr);
   }
  }
  @media screen and (min-width: 600px) and (max-height: 500px) {
    .game .cards-container {
      grid-template-columns: repeat(9, auto);
      grid-template-rows: repeat(2, 1fr);
   }
  }
  @media (max-width: 1024px) {
    .game .cards-container {
      width: calc(100vw - (1024 * 2));
      height: calc(100vh - (1024 * 2));
      margin: 16px;
      grid-gap: 8px;
   }
  }
  
